th[sortable] {
    cursor: pointer;
}

th[sortable].asc:after {
    content: "";
    display: block;
    background: url(data:image/png;base64,) no-repeat;
    background-size: 22px;
    width: 22px;
    height: 22px;
    float: right;
}

th[sortable].desc:after {
  content: "";
  display: block;
  background: url(data:image/png;base64,) no-repeat;
  background-size: 22px;
  width: 22px;
  height: 22px;
  float: right;
  transform: rotate(180deg);
}

.table {
  --bs-table-color-state: var(--bs-body-color);
  --bs-table-bg: #d3d3d3;
}


